<template>
  <div class="login-container">
        <el-form :model="registerData" :rules="rules" ref="registerForm" class="register-form">

        <div class="logo">
          <img style="background-size: 100% 100%;width: 100%;height: 100%;" src="@/assets/logo大.png" alt="">
        </div>

        <el-form-item prop="username">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-user-solid"></i>
              </i>
            </span>
            <el-input 
              v-model="registerData.username"
              placeholder="Username"
              name="username"
              type="text"
              tabindex="1"
              auto-complete="on"
            ></el-input>
          </div>
        </el-form-item>

        
        <el-form-item prop="password">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-lock"></i>
              </i>
            </span>
            <el-input 
              key="password"
              ref="password"
              v-model="registerData.password"
              placeholder="Password"
              name="password"
              type="password"
              tabindex="2"
              auto-complete="on"
              show-password
            ></el-input>
          </div>
        </el-form-item>

        <el-form-item prop="password2">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-key"></i>
              </i>
            </span>
            <el-input 
              key="password2"
              ref="password2"
              v-model="registerData.password2"
              placeholder="Repassword"
              name="password2"
              type="password2"
              tabindex="3"
              auto-complete="on"
              show-password
            ></el-input>
          </div>
        </el-form-item>

        <el-form-item prop="nickname">
          <div class="el-container">
            <span class="icon-container">
              <i>
                <i class="el-icon-postcard"></i>
              </i>
            </span>
            <el-input 
              ref="nickname"
              v-model="registerData.nickname"
              placeholder="Nickname"
              name="nickname"
              type="nickname"
              tabindex="3"
              auto-complete="on"
            ></el-input>
          </div>
        </el-form-item>

        <div class="item-buttton">
          <el-button type="primary" style="width: 100%;margin-bottom:30px;" @click="submitForm('registerForm')">注册</el-button>
        </div>
        <div>
          <el-link @click="ToLogin()" type="info">已有帐号？前往登录></el-link>
        </div>
      </el-form>
      </div>
</template>

<script>
import { registerAPI } from '@/api'
export default {
  name: 'register',
  data() {
    // 验证账号
    let username = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入账号"));
      } else {
        callback();
      }
    };
    // 验证密码
    var validatePass = (rule, value, callback) => {
      if (value !== this.registerData.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      registerData:{
        username:'',
        password:'',
        password2:'',
        nickname:''
      },
      rules:{
        username:[
          {required:true, message:'账号不能为空', trigger:'blur'},
          {
            pattern: /^(?!(\d+)$)[a-zA-Z\d_]{4,20}$/,
            message: "账号长度4-20，必须包括数字、字母，可包括下划线",
            trigger: "blur"
          },
          { validator: username, trigger: "blur" }
        ],
        password:[
          {required:true, message:'密码不能为空', trigger:'blur'},
          {
            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
            message: "密码长度为6-20位，必须且只能包括数字、字母",
            trigger: "blur"
          }
        ],
        password2:[
          {required:true, message:'确认密码不能为空', trigger:'blur'},
          {
            pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
            message: "密码长度为6-20位，必须且只能包括数字、字母",
            trigger: "blur"
          },
          {validator: validatePass, trigger: 'blur'}
        ],
        nickname:[
          {required:true, message:'用户名不能为空', trigger:'blur'},
          {min:2, max:32, message:'长度应在2-32之间',trigger:'blur'}
        ]
      }
    };
  },
  methods: {
    //注册
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          console.log(this.registerData);
          const { data: res } = await registerAPI({
            username:this.registerData.username,
            password:this.registerData.password,
            nickname:this.registerData.nickname
          })
          console.log(res);
          if(res.status !== 0) return this.$message.error(res.message)
          this.$message({
            type: 'success',
            message:'注册成功'
          })
          this.$router.push('/home')
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    ToLogin(){
      this.$router.push('/home')
    }
  },
};
</script>

<style scoped>
.login-container{
    min-height: 100%;
    width: 100%;
    /* background: url('@/assets/loginBG.jpg'); */
    background: url('@/assets/中草药滚动背景.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 140% 100%;
    animation: gradientBG 30s ease infinite;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    flex-direction: column;
    -webkit-display:flex;
    display:flex;
    overflow: hidden;
}
@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.register-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 20px 35px 20px 35px;
    margin: 160px auto;
    overflow: hidden;
    box-sizing: border-box;
    border-radius: 10px;
}
.logo{
  background-image: linear-gradient(to right, rgb(89, 89, 89),rgb(255, 255, 255));
  -webkit-background-clip: text;
  color: transparent;
  width: 170px;
  height: 90px;
  margin: 0 auto;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: -1px;
  transition: all 0.3s linear;
  text-align: center;
}
.logo span{
  /* background-image: linear-gradient(to right, rgb(99, 77, 31), rgb(205, 162, 106)); */
  /* background-image: linear-gradient(to right, rgb(0, 162, 255), rgb(0, 128, 6)); */
  -webkit-background-clip: text;
  color: transparent;
  font-size: 30px;
  transition: all 0.1s linear;
}
.el-form-item {
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
  margin: 26px 0;
}
.el-container{
    display: flex;
    border-radius: 5px;
}
.icon-container{
    padding: 0 10px;
    color: #fff;
    font-size: 20px;
}
.el-input /deep/ .el-input__inner{
    border: none;
    background: #0000;
    color: #fff;
}
.el-link.el-link--info {
  color: #fff;
  font-size: 16px;
}
/* .item-buttton {
  display: flex;
  justify-content: center;
  margin: 50px 25px 30px 100px;
}
.el-checkbox:last-of-type {
  margin-left: 30px;
}
.el-button--primary {
  width: 360px;
} */
</style>